.about-title {
  font-family: Bebas Neue;
  font-size: 80px;
  font-weight: 400;
  line-height: 87px;
  letter-spacing: 0em;
  text-align: left;
  padding-top: 3rem;
}
.about-description {
  font-family: "Roboto Condensed";
  font-size: 16px;
  font-weight: 300;
  line-height: 18px;
  letter-spacing: -0.05em;
  text-align: justify;
  mix-blend-mode: screen;
}
.about-content {
  display: grid;
  grid-template-columns: 40% 60%;
  gap: 2rem;
  overflow: hidden;
}
.about-images {
  display: grid;
  gap: 2rem;
  height: 90%;
}
.img {
  width: 100%;
  height: 100%;
  margin: auto;
  overflow: hidden;
}
.img > img {
  width: 100%;
}
